import {AfterViewInit, Component} from '@angular/core';

@Component({
  selector: 'ngx-single-application',
  template: `
    <div  echarts [options]="options" theme = "light" class="echart"></div>
  `,
})
export class SingleApplicationComponent implements AfterViewInit {
  options: any = {};

  ngAfterViewInit() {
    this.options = {
      tooltip: {
        trigger: 'item',
        formatter: '{a} <br/>{b}: {c} ({d}%)',
      },
      legend: {
        orient: 'vertical',
        x: 'left',
        data: ['', '', '', '', '', '', '', '', '', ''],
      },
      series: [
        {
          name: '',
          type: 'pie',
          selectedMode: 'single',
          radius: [0, '30%'],

          label: {
            normal: {
              position: 'inner',
            },
          },
          labelLine: {
            normal: {
              show: false,
            },
          },
          data: [
            {value: 335, name: '', selected: true},
            {value: 679, name: ''},
            {value: 1548, name: ''},
          ],
        },
        {
          name: '',
          type: 'pie',
          radius: ['40%', '55%'],
          label: {
            normal: {
              formatter: '{a|{a}}{abg|}\n{hr|}\n  {b|{b}}{c}  {per|{d}%}  ',
              backgroundColor: '#eee',
              borderColor: '#aaa',
              borderWidth: 1,
              borderRadius: 4,
              // shadowBlur:3,
              // shadowOffsetX: 2,
              // shadowOffsetY: 2,
              // shadowColor: '#999',
              // padding: [0, 7],
              rich: {
                a: {
                  color: '#999',
                  lineHeight: 22,
                  align: 'center',
                },
                // abg: {
                //     backgroundColor: '#333',
                //     width: '100%',
                //     align: 'right',
                //     height: 22,
                //     borderRadius: [4, 4, 0, 0]
                // },
                hr: {
                  borderColor: '#aaa',
                  width: '100%',
                  borderWidth: 0.5,
                  height: 0,
                },
                b: {
                  fontSize: 16,
                  lineHeight: 33,
                },
                per: {
                  /* color: '#eee',*/
                  /*backgroundColor: '#334455',*/
                  padding: [2, 4],
                  borderRadius: 2,
                },
              },
            },
          },
          data: [
            {value: 335, name: ''},
            {value: 310, name: ''},
            {value: 234, name: ''},
            {value: 135, name: ''},
            {value: 1048, name: ''},
            {value: 251, name: ''},
            {value: 147, name: ''},
            {value: 102, name: ''},
          ],
        },
      ],
    };
  }
}



